<template>
  <div class="Subject">
    <swper :swper-list="swper" />
    <div class="conten">
      <div class="games" v-for="(item, index) in games" :key="index">
        <div class="header">
          <div class="title">{{ item.title }}</div>
          <div class="detail" @click="gotoDetail(item)">
            <span>查看专题详情</span>
            <svg-icon size="20" icon-class="right_arrow_two" />
          </div>
        </div>
        <div class="body">
          <img-arrangement :type="item.type" :data="item.img" />
        </div>
        <div class="name">{{ item.name }}</div>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import { mapState } from "vuex";
import { getHome } from "@/api/home.js";
export default {
  name: "Subject",
  components: {
    imgArrangement: () => import("./imgArrangement.vue")
  },
  data() {
    return {
      hasFooter: true,
      swper: [],
      games: [
        {
          id: "11111",
          name: "漫漫长城",
          title: "适合冬天围着火炉玩的游戏",
          type: "type1",
          img: [
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            },
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            }
          ]
        },
        {
          id: "11111",
          name: "漫漫长城",
          title: "适合冬天围着火炉玩的游戏",
          type: "type2",
          img: [
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            },
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            },
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            }
          ]
