          type: "type4",
          img: [
            {
              url: "https://img2.baidu.com/it/u=2130207220,3427500529&fm=26&fmt=auto"
            }
          ]
        }
      ]
    };
  },
  created() {
    this.getSlideshow();
  },
  computed: {
    ...mapState("home", ["slideshow"])
  },
  methods: {
    async getSlideshow() {
      this.swper = this.slideshow;
      if (!this.slideshow.length) {
        getHome().then(res => {
          this.swper = res.data.slide;
        });
      }
    },
    gotoDetail(info) {
      this.$router.push({
        path: "/Detail",
        query: { id: info.id }
      });
    }
  }
};
</script>

<style lang="less" scoped>
  .Subject {
    .games {
      padding: 30px 20px;
      margin-top: 30px;
      background-color: rgba(255, 255, 255, 0.02);
      .header {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px dashed #808081 ;
        padding-bottom: 18px;
        .title {
          color: #fff;
          font-size: 24px;
          font-weight: bold;
        }
        .detail {
          cursor: pointer;
          color: @warn;
          font-size: 16px;
          display: flex;
          align-items: center;
          span {
