import request from "@/utils/request";

export function getHome(params) {
  return request({
    url: "/index/index",
    method: "post",
    params
  });
}

//首页获取更多游戏数据
export function indexGetMoreGame(params){
  return request({
    url: "/index/moreGames",
    method: "post",
    params
  });
}

//首页获取幻灯片数据
export function getSlideData(params){
  return request({
    url: "/index/slide",
    method: "post",
    params
  });
}
